import React, { Component } from 'react';
const $ = window.$;
class aboutme extends Component {
    componentDidMount()
    {
        setTimeout(() => {
            
        
        var DEFAULTS = {
			backgroundColor: '#b3cef6',
			progressColor: '#FFA600',
			percent: 75,
			duration: 2000,
			subject:"Angular"
		};	
		
		$(".progress-bar").each(function () {
			var $target  = $(".progress-bar");

			var opts = {
			backgroundColor: $target.data('color') ? $target.data('color').split(',')[0] : DEFAULTS.backgroundColor,
			progressColor: $target.data('color') ? $target.data('color').split(',')[1] : DEFAULTS.progressColor,
			percent: $target.data('percent') ? $target.data('percent') : DEFAULTS.percent,
			duration: $target.data('duration') ? $target.data('duration') : DEFAULTS.duration,
			subject: $target.data('subject') ? $target.data('subject') : DEFAULTS.subject
			};
			// console.log(opts);
	
			$target.append('<div class="background"></div><div class="rotate"></div><div class="left"></div><div class="right"></div><div class=""><span>' + opts.percent + '% '+ opts.subject +' </span></div>');
	
			$target.find('.background').css('background-color', opts.backgroundColor);
			$target.find('.left').css('background-color', opts.backgroundColor);
			$target.find('.rotate').css('background-color', opts.progressColor);
			$target.find('.right').css('background-color', opts.progressColor);
	
			var $rotate = $target.find('.rotate');
			setTimeout(function () {	
				$rotate.css({
					'transition': 'transform ' + opts.duration + 'ms linear',
					'transform': 'rotate(' + opts.percent * 3.6 + 'deg)'
				});
			},1);		

			if (opts.percent > 50) {
				var animationRight = 'toggle ' + (opts.duration / opts.percent * 50) + 'ms step-end';
				var animationLeft = 'toggle ' + (opts.duration / opts.percent * 50) + 'ms step-start';  
				$target.find('.right').css({
					animation: animationRight,
					opacity: 1
				});
				$target.find('.left').css({
					animation: animationLeft,
					opacity: 0
				});
			} 
        });
    }, 2000);
    }
    render() {

        return (
         
                <section id="one">
					<ul className="actions" style={{float:"right"}}>
							<li><a href="#" className="button" style={{border: "1px solid #FFA600"}}>Download CV</a></li>
						</ul>
						<header className="major">
							<h2>About me </h2>
						</header>
						<p className="margin-b-50">I am a <b>MEAN-Stack</b> web developer with over 3.5 years of experience in Industry. I am currently working as Software Engineer in <b>Winjit Technologies</b> improving products and services for our customers by using Emerging Technologies.

						My career objective is to pursue a challenging career in a progressive environment where learning, innovation, and creativity are encouraged, where my skills and knowledge can be enhanced to their
						maximum potential to contribute to the overall success and growth of the organization.
						
						Technical Skills:
						Recently working on <b>Angular2, Angular4 ,Angular6, Nodejs, Express, Mongodb</b>
						Previously worked on AngularJS, JavaScript,NodeJS, ExpressJS
						 **Learning more about above-said skills as well as other technologies like **
						Algorithms and Data Structure, AWS, Firebase, Google Cloud Platform</p>
						<div  style={{width: "100%",float: "left"}}><div  className="progress-bar-main" style={{width:"25%",float:"left"}} >
                            <div className="progress-bar position" data-percent="85 " data-subject="Angular" data-color="#f3f3f3,#FFA600"></div></div>
	<div  style={{width:"25%",float:"left"}} className="progress-bar-main"><div className="progress-bar position" data-percent="65" data-subject="Node" data-duration="1000" data-color="#f3f3f3,#FFA600"></div></div>
	<div style={{width:"25%",float:"left"}} className="progress-bar-main"><div className="progress-bar position" data-percent="75" data-subject="Javascript" data-color="#f3f3f3,#FFA600"></div></div>
	<div  style={{width:"25%",float:"left"}} className="progress-bar-main"><div className="progress-bar position" data-percent="50" data-subject="React" data-color="#f3f3f3,#FFA600"></div></div>
	</div>
					</section>
           
        );
    }
}

export default aboutme;