import React, { Component } from 'react';
import Education from './education'
import Award from './award'

class experiance extends Component {
    constructor(props) {
        super(props);
        this.state = {
            experiance: [
                {
                    designation: "Software Engineer",
                    compnayName: "Winjit Technologies Pvt. Ltd",
                    duration: "Jan. 2018 - PRESENT",
                    role: ["Worked  on Angular6,Build customer-facing UI and back-end service.", "Code,test and operate Node.js based services.",
                        "Writing Reusable,testable,and efficient code.", "Code, test and operate Nodejs based back-end services."]
                },
                {
                    designation: "Software Devoloper",
                    compnayName: "Vega Innovations Pvt. Ltd",
                    duration: "Jan. 2018 - PRESENT",
                    role: ["Worked  on Angular6,Build customer-facing UI and back-end service.", "Code, test and operate Nodejs based back-end services."]
                },
                {
                    designation: "Software Devoloper",
                    compnayName: "Zobble Solution Pvt. Ltd.",
                    duration: "Jan. 2018 - PRESENT",
                    role: ["Worked  on Angular6,Build customer-facing UI and back-end service.", "Code,test and operate Node.js based services.",
                        "Writing Reusable,testable,and efficient code.", "Optimize application for maximum speed and scalability.", "Code, test and operate Nodejs based back-end services."]
                },
            ]
        }
    }
    render() {

        const renderRoles = (roles) => {
            return roles.map((itemRoles) => {
                return (
                    <li>{itemRoles}</li>
                )
            })
        }
        const renderData = () => {

            return this.state.experiance.map((item) => {
                return (
                    <article className="col-6 col-12-xsmall work-item">
                        <h3>{item.designation}</h3>
                        <h4>{item.compnayName}</h4>
                        <h5>{item.duration}</h5>
                        <ul className="list margin-b-30">
                            {renderRoles(item.role)}
                        </ul>
                    </article>
                )
            })
        }
        return (
            <section id="two">
                <h2>Work Experience</h2>
                <div className="row">
                    {
                        renderData()
                    }
                </div><hr />
                <Education />
                <Award />
            </section>

        );
    }
}

export default experiance;